import React, { Component } from 'react'
import Modal from 'react-modal'
import PropTypes from 'prop-types'
import Spinner from 'react-spinkit'
import merge from 'deepmerge'
import font from './assets/css/fonts.css'

/**
_________ MODAL _________
|                       |
| ______ CONTENT ______ |
| |                   | |
| | _____ INNER _____ | |
| | |               | | |
| | |     TITLE     | | |
| | |    SPINNER    | | |
| | |   SUB TITLE   | | |
| | |   CHILDREN    | | |
| | |_______________| | |
| |___________________| |
|_______________________|
**/

export const SPINNER_TYPES = [
  'circle',
  'cube-grid',
  'wave',
  'folding-cube',
  'three-bounce',
  'double-bounce',
  'wandering-cubes',
  'chasing-dots',
  'rotating-plane',
  'pulse',
  'wordpress',
  'ball-grid-beat',
  'ball-grid-pulse',
  'line-spin-fade-spinner',
  'ball-spin-fade-loader',
  'ball-pulse-rise',
  'line-scale',
  'line-scale-pulse-out',
  'line-scale-pulse-out-rapid',
  'pacman',
  'line-scale-party',
  'ball-triangle-path',
  'ball-scale-multiple',
  'ball-scale-ripple-multiple',
  'ball-pulse-sync',
  'ball-beat',
  'ball-zig-zag',
  'ball-zig-zag-deflect',
  'ball-clip-rotate-pulse',
  'ball-clip-rotate-multiple',
  'ball-clip-rotate',
  'ball-scale-ripple',
  'triangle-skew-spin'
]

const defaultTheme = {
  outer: {
    backgroundColor: 'rgba(255, 102, 0, 1)'
  },
  content: {
    background: 'transparent',
    border: 0,
    padding: 0,
    top: 0,
    left: 15,
    right: 15,
    bottom: 0,
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'center',
    color: 'white',
    fontFamily: 'Montserrat',
    fontStyle: 'bold'
  },
  inner: {
    display: 'flex',
    flexDirection: 'column',
    alignItems: 'center',
    textAlign: 'center'
  },
  title: {
    marginBottom: '1em',
    letterSpacing: '6px',
    textTransform: 'uppercase'
  },
  subTitle: {
    marginTop: '1.2em',
    letterSpacing: '0.1px',
    wordWrap: 'wrap',
    fontStyle: 'italic',
    fontFamily: 'EB Garamond'
  }
}

const convertTheme = ({ outer, content, ...original }) => ({
  ...original,
  modal: { overlay: outer, content }
})

const SpinnerResolver = ({ type, config }) =>
  typeof type === 'string' ? (
    <Spinner
      name={type}
      color={config.color}
      fadeIn={config.fadeIn}
      className={config.className}
      overrideSpinnerClassName={config.overrideSpinnerClassName}
    />
  ) : (
    type
  )

const withTheme = (WrappedComponent, baseTheme, converter) => {
  return class extends Component {
    static propTypes = {
      theme: PropTypes.object
    }

    state = {
      theme: baseTheme || {},
      lastUpdate: {}
    }

    constructor(props) {
      super(props)

      this.applyTheme = this.applyTheme.bind(this)
    }

    componentWillMount() {
      this.applyTheme(this.props.theme)
    }

    componentWillReceiveProps(nextProps) {
      if (nextProps.theme && this.state.lastUpdate !== nextProps.theme) {
        this.applyTheme(nextProps.theme)
      }
    }

    applyTheme(changes = {}) {
      this.setState({
        theme: converter
          ? converter(merge(baseTheme, changes))
          : merge(baseTheme, changes),
        lastUpdate: changes
      })
    }

    render() {
      return (
        <WrappedComponent
          applyTheme={this.applyTheme}
          {...this.props}
          theme={this.state.theme}
        />
      )
    }
  }
}

const LoadingModal = ({
  children,
  title,
  subTitle,
  spinner,
  spinnerConfig,
  theme,
  ...props
}) => (
  <Modal {...props} style={theme.modal} ariaHideApp={false}>
    <div style={theme.inner}>
      <h1 style={theme.title}>{title}</h1>
      <SpinnerResolver type={spinner} config={spinnerConfig} />
      <h2 style={theme.subTitle}>{subTitle}</h2>
      {children}
    </div>
  </Modal>
)

LoadingModal.propTypes = {
  spinner: PropTypes.oneOfType([
    PropTypes.oneOf(SPINNER_TYPES),
    PropTypes.element
  ]),
  spinnerConfig: PropTypes.shape({
    fadeIn: PropTypes.oneOf(['none', 'quarter', 'half']),
    className: PropTypes.string,
    overrideSpinnerClassName: PropTypes.string,
    color: PropTypes.string
  })
}

LoadingModal.defaultProps = {
  spinner: 'line-scale-pulse-out',
  spinnerConfig: {
    color: 'white'
  }
}

export default withTheme(LoadingModal, defaultTheme, convertTheme)
